$background_color: #124076;
$lighter_shade: #fcf4f7;
.role_mangement {
  width: 100%;
  height: 100vh;
  padding-top: 60px;
  .inner_role_mangement {
    width: 100%;
    height: 100%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    .roles {
      width: 20%;
      height: 100%;
      table {
        width: 100%;
        height: fit-content;
        border-collapse: collapse;
        tbody {
          .column_value {
            th {
              text-align: left;
              font-weight: 400;
              font-size: 1vw;
              padding: 8px 15px;
            }
          }
          .column_name {
            th {
              text-align: left;
              padding: 8px 15px;
              font-size: 1vw;
            }
          }
        }
      }
    }
    .create_edit {
      width: 80%;
      height: 100%;
      // border: 1px solid black;
      border-radius: 5px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      .button {
        width: 100%;
        height: fit-content;
        padding: 5px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 10px;
        .normal {
          padding: 8px 15px;
          border-radius: 5px;
          border: none;
          box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
            rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
          cursor: pointer;
          font-size: 1vw;
        }
        .colour {
          font-size: 1vw;
          padding: 8px 15px;
          border-radius: 5px;
          border: none;
          box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
            rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
          cursor: pointer;
          background-color: $background_color;
          color: $lighter_shade;
        }
      }
      .create_section {
        width: 100%;
        height: 90%;
        padding: 5px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        gap: 10px;
        .add_role {
          width: 100%;
          height: fit-content;
         
          input{
            padding: 5px 16px;
            outline: none;
            font-size: 1vw;

          }
        }
        .add_permissions {
          width: 100%;
          height: 80%;
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          gap: 10px;
          .list_of_permissions {
            width: 50%;
            height: 100%;
            overflow-y: scroll;
            border: 1px solid black;
            table {
              width: 100%;
              height: 100%;
              border-collapse: collapse;
              font-size: 1vw;
              tbody {
                .column_name {
                  th {
                    padding: 8px 15px;
                  }
                }
                .selected {
                  background-color: gray;
                }
                .column_value {
                  th {
                    padding: 8px 15px;
                    text-align: left;
                    font-weight: 400;
                  }
                }
              }
            }
          }
          .added_list_of_permissions {
            width: 50%;
            height: 100%;
            overflow-y: scroll;
            border: 1px solid black;
            table {
              width: 100%;
              height: fit-content;
              border-collapse: collapse;
              font-size: 1vw;
              tbody {
                .column_name {
                  th {
                    padding: 8px 15px;
                  }
                }
                .column_value {
                  th {
                    padding: 8px 15px;
                    text-align: left;
                    font-weight: 400;
                  }
                }
              }
            }
          }

        }
        .button_{
           
            button{
                padding: 8px 15px;
                border-radius: 5px;
                border: none;
                box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
                  rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
                cursor: pointer;
                background-color: $background_color;
                color: $lighter_shade;
                font-size: 1vw;
            }
        }
      }
    }
  }
}



@media screen and (max-width:2559px) and (min-width:1920px) {
  .role_mangement{
      padding-top: 80px;
  }
}

@media screen and (min-width:2560px) {
  .role_mangement{
      padding-top:80px ;
  }
}